body{
    margin: 0 auto;
    padding: 0;
    height: 100%;
    width: 100%;

    max-width: 500px;
    max-height: 1000px;
    
    display: block;

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
   
    font-family: Arial;
    text-align: center;
    
}

.topbar3{
    margin: 1%;
    width: 98%;
    max-width: 500px;
    height: 5%;

    top: 1%;
    position: absolute;

    vertical-align: middle;

    object-fit: contain;

    background: url("https://i.ibb.co/8Mg4RSx/TOPBAR-5.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    
}

